const errSystem = (err) => {
    let message = '';
    switch (err) {

        // error LOGIN
        case 'SYS001': message = "Email va mt khu khng c  trng"; break;
        case 'SYS002': message = "inh dang email khng hp l"; break;
        case 'SYS003': message = "Mt khu khng u manh. Bao gm ky t vit hoa va s"; break;
        case 'SYS004': message = "Tai khoan cha c kich hoat"; break;
        case 'SYS005': message = "Tai khoan hot mt khu khng chinh xat"; break;

        case 'SYS006': message = "Vui long in y u thng tin"; break;
        case 'SYS007': message = "Mt khu khng khp"; break;
        case 'SYS008': message = "Email a tn tai"; break;

        case 'SYS009': message = "Vui long in ma xat thc"; break;

        // register mentor
        case 'RMEMBER001': message = "Vui long in tn hin thi"; break;
        case 'RMEMBER002': message = "Vui long in trng ang hoc"; break;
        case 'RMEMBER003': message = "Vui long chon chuyn nganh "; break;
        case 'RMEMBER004': message = "Vui long chon hoc ki"; break;
        case 'RMEMBER005': message = "Vui long in m ta"; break;
        case 'RMEMBER006': message = "Vui long in gia thu"; break;
        case 'RMEMBER007': message = "Vui long in link skype"; break;
        case 'RMEMBER008': message = "Vui long chon anh ai din"; break;
        case 'RMEMBER009': message = "Chi phi phai la s"; break;

        case "SERVER001": message = "Ma code khng chinh xat hoc thi gian a ht han"; break;
        case "SERVER002": message = "Li khng xat inh"; break;
        default: message = "Li khng xat inh"; break;
    }
    return message
}

